import { NgModule } from '@angular/core';
import { PreferencesComponent } from './preferences.component';
import { RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: '',
    component: PreferencesComponent,
    children: [
      {
        path: 'business',
        loadChildren: () =>
          import('./business/business.module').then((m) => m.BusinessModule),
      },
      {
        path: 'branch',
        loadChildren: () =>
          import('./branch-info/branch-info.module').then(
            (m) => m.BranchInfoModule
          ),
      },
      {
        path: 'amenities',
        loadChildren: () =>
          import('./amenities/amenities.module').then((m) => m.AmenitiesModule),
      },
      {
        path: 'images',
        loadChildren: () =>
          import('./images/images.module').then((m) => m.ImagesModule),
      },
      {
        path: 'working-hours',
        loadChildren: () =>
          import('./schedules/schedules.module').then((m) => m.SchedulesModule),
      },
      {
        path: 'payment',
        loadChildren: () =>
          import('./payment/payment.module').then((m) => m.PaymentModule),
      },
      {
        path: '',
        redirectTo: 'business',
        pathMatch: 'full',
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PreferencesRoutingModule {}
